import _ from 'lodash';
import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { withRouter } from "react-router-dom";
import { chatMsgRef } from "firebaseUtil/firebase";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import ChatMessage from "components/Chat/ChatMessage";
import ChatInput from "components/Chat/ChatInput";
import { avatarUser, mediaTitle } from 'Util/chatUtil';
import FloatyAssistant from 'components/Assistant/floaty';
import sectionCommentsStyle from "assets/jss/material-kit-pro-react/views/blogPostSections/sectionCommentsStyle.js";
import { db } from 'firebaseUtil/firebase';
import { reduxDispatch } from 'redux/store';
import * as chatTypes from "redux/module/chatModule/action"
import { child, limitToLast, onValue, push, query, ref as DatabaseRef, remove, set, update } from 'firebase/database';
import { signOut } from 'firebase/auth';
import { auth } from 'firebaseUtil/firebase';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const useStyles = makeStyles(sectionCommentsStyle);

const AdminChatPage = ({ history }) => {
    const classes = useStyles();
    const { user } = useSelector(state => state.login);
    const [allChatMessageInfo, setAllChatMessageInfo] = useState(undefined);
    const [replyMessage, setReplyMessage] = useState("");
    const [replyImageUrl, setReplyImageUrl] = useState("");
    const { maxMessageNumber } = useSelector(state => state.chat);

    useEffect(() => {
        if (chatMsgRef && maxMessageNumber !== undefined) {
            const limitChatMsgRef = query(chatMsgRef, limitToLast(maxMessageNumber));
            onValue(limitChatMsgRef, (snapshot) => {
                messageUpdate(snapshot);
            });
        }
    }, [maxMessageNumber])

    useEffect(() => {
        if (user && replyImageUrl && replyImageUrl !== "") {
            const messageInfo = {
                user: user,
                message: `${replyImageUrl}`,
                createOn: Date.now()
            }

            const newPostKey = push(child(DatabaseRef(db), 'chatMsg')).key;

            let data = {};
            data[newPostKey] = messageInfo;

            update(chatMsgRef, data)
                .then(() => {
                    // Data saved successfully!
                })
                .catch((error) => {
                    // The write failed...
                    console.log(error.message);
                    toast.error(error.message);
                });
        }
    }, [replyImageUrl])

    const messageUpdate = (snapshot) => {
        const data = snapshot.val();
        if (data !== null && data !== undefined) {
            // 最多顯示 70 則訊息
            let sliceData = _.slice(_.values(data), -maxMessageNumber);

            //加入 firebase 參考用 ID
            sliceData.forEach(item => {
                const rawObj = _.pickBy(data, function (value, key) {
                    return value.createOn === item.createOn;
                });

                if (rawObj) {
                    const id = Object.keys(rawObj)[0];
                    item.id = id;
                }
            });

            // 最新一則 Po 文的使用者
            const currentSendMsgUser = _.slice(_.values(sliceData), -1)[0].user;

            // 最新一則 Po 文的訊息 ID (改採用 Po 文時間)
            const currentSendMsgId = _.slice(_.values(sliceData), -1)[0].createOn;

            // 若最新 Po 文的使用者不是自己(當下登入帳號), 則不要將 Reply Message 清空, 以免尚未回覆的訊息消失
            if (user === currentSendMsgUser) {
                setReplyMessage("");
                setAllChatMessageInfo(sliceData);
            }
            else {
                setAllChatMessageInfo(sliceData);
            }
        }
    }

    const onMessageDelete = (msgId, intervalObject) => {
        try {
            if (msgId) {
                const isDelete = window.confirm("是否刪除?");
                if (isDelete) {

                    remove(child(DatabaseRef(db), `chatMsg/${msgId}`));
                }

                if (intervalObject) {
                    clearInterval(intervalObject);
                }
            } else {
                console.log("msgId not exist");
            }
        } catch (error) {
            console.log(error);
        }
    }

    const onReplyClick = event => {
        if (user && replyMessage !== "") {
            const messageInfo = {
                user: user,
                message: replyMessage,
                createOn: Date.now()
            }

            const newPostKey = push(child(DatabaseRef(db), 'chatMsg')).key;

            let data = {};
            data[newPostKey] = messageInfo;

            update(chatMsgRef, data)
                .then(() => {
                    // Data saved successfully!
                })
                .catch((error) => {
                    // The write failed...
                    console.log(`訊息無法送出`);
                    console.log(error.message);
                    toast.error(error.message);
                });
        }
    }

    const onUpdateClick = event => {
        if (user) {
            const number = maxMessageNumber + 1;
            reduxDispatch(chatTypes.SET_MAX_MESSAGE_NUMBER, number);
        }
    }

    return (
        <div className={classes.section}>
            <GridContainer justify="center">
                <GridItem xs={12} sm={8} md={8}>
                    <div>
                        {allChatMessageInfo ? allChatMessageInfo.map((value, index) => {
                            return (
                                <ChatMessage
                                    key={value.id !== undefined ? value.id : index}
                                    avatar={avatarUser(value.user)}
                                    title={mediaTitle(value)}
                                    message={value.message}
                                    id={value.id}
                                    event={onMessageDelete}
                                />
                            )
                        }) : <div></div>}
                        <ChatInput
                            avatar={avatarUser(user)}
                            value={replyMessage}
                            onChange={(e) => { setReplyMessage(e.target.value) }}
                            onReply={onReplyClick}
                            onUpdate={onUpdateClick}
                            enableUpdate={true}
                            setReplyImageUrl={setReplyImageUrl}
                            enableImageUpload={true}
                        />
                    </div>
                </GridItem>
            </GridContainer>
            <FloatyAssistant></FloatyAssistant>
            <ToastContainer
                theme='dark'
                position="bottom-center"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
            />
        </div>
    );
}

export default withRouter(AdminChatPage);