import React from 'react';
import { carlos, linda } from "firebaseUtil/user";
import marc from "assets/img/faces/marc.jpg";
import gloria from "assets/img/faces/gloria2.jpg";
import jerry from "assets/img/faces/jerry.jpg";

const chatFontSize = {
    fontSize: '0.7rem'
}

const getMessageTimeNotify = (utc) => {
    const localDate = new Date(utc);
    const localHr = switchNumber(localDate.getHours());
    const localMin = switchNumber(localDate.getMinutes());

    const current = Date.now();
    const diffUtc = parseInt(current - utc, 10);
    const min = (diffUtc / 1000) / 60;
    const hr = min / 60;
    const day = hr / 24;

    if (min <= 60) {
        // return `${Math.floor(min)} minutes ago`;
        return `${localHr}:${localMin}`;
    } else if (min > 60 && hr <= 24) {
        // return `${Math.floor(hr)} hours ago`;
        // return `${localHr}:${localMin}`;
        return `${localHr}:${localMin} ----- ${localDate.toDateString()}`;
    } else if (hr > 24) {
        // return `${Math.floor(day)} days ago`;
        return `${localHr}:${localMin} ----- ${localDate.toDateString()}`;
    }

    return min;
}

const switchNumber = (num) => {
    const value = num.toString();
    if (value.length === 1) {
        return `0${value}`;
    } else {
        return value;
    }
}

const avatarUser = (arg) => {
    if (arg === linda) {
        return gloria;
    } else if (arg === carlos) {
        return jerry;
    } else {
        return marc;
    }
}

const mediaTitle = (value) => {
    if (value.user === linda) {
        return (
            <span style={chatFontSize}>
                Karen <small>· {getMessageTimeNotify(value.createOn)} </small>
            </span>
        );
    } else if (value.user === carlos) {
        return (
            <span style={chatFontSize}>
                Kevin <small>· {getMessageTimeNotify(value.createOn)}</small>
            </span>
        );
    } else {
        return (<></>);
    }
}

export { mediaTitle, avatarUser }